html, body {
  font-family: sans-serif;
  font-size: 16px;
  background: #FFf;
  font-family: 'Roboto', sans-serif;
} 

.wrapper {
  width: 100%;
  height: 100%;
}
.conteiner {
  max-width: 1170px;
  display: flex;
  justify-content: space-between;
  margin: auto;
}
body ,html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 0;
  background: #000;
}

.gradient-btn {
  transform: translate(-50%, -50%);
  width: auto;
  height: auto;
  text-align: center;
  font-size: 20px;
  color: #fff;
  text-decoration: none;
  transition: .5s;
  background-color: transparent;
  border: 2px solid #fff;
  border-radius: 60px;
  box-shadow: 0 5px 20px rgba(0,0,0, .5);
  overflow: hidden;
  padding: .5rem 1rem  ;

}
.g {
  margin-left: 8rem;
}
.gradient-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to left, #9700fb,#1366ff);
  z-index: -1;
  border-radius: 60px;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform .5s ease-in-out;
}
.gradient-btn:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}
