.main_wrapper {
  background: url(../../assets/img/backgrond1920x2800nogamepad.jpg) top/cover;
  background-repeat: no-repeat;
  color: #fff;
  padding-top: 10rem;

}
.title {
  font-size: 50px;
  font-family: 'Roboto', sans-serif;

}
.subtitle {
  font-size: 25px;
  
}
.news_cards {
  height: 28rem;
  
  
}
.news_cards_wrapper {
  display: flex;
  margin-bottom: 3rem;
}
.m {
  margin-right: 4rem;
}
.news_cards_wrapper {
  max-width: 1170px;
  margin: auto;
}
.btn_wrapper {
  margin-bottom: 15rem;
}
.news_card{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main {
  margin-left: 10rem;
  display: flex;
  justify-content: space-between;
}
.btn {
  width: 8rem ;
  height: 2.5rem ;
  font-size: 17px;
  background-color: transparent;
  border-radius: 15px;
  outline-style: none;
  border: solid #fff 1px;
  color: #fff;
  transition: all 300ms ease;
}
:hover.btn { 
  background: linear-gradient(90deg, rgba(102,0,170,1) 0%, rgba(29,244,253,1) 93%);
  transition: all 300ms ease;
}
.to_news {
  color: #000;
  text-decoration: none;
  background-color: #c2c2c2;
  padding: .6rem;
  font-weight: bold;
  text-align: center;
  width: auto;
}
svg path {
  animation: colored .6s infinite  ;
}
svg path:nth-child(2) {
  animation-delay: 1.5s;
}
svg path:nth-child(4) {
  animation-delay: 1s;
}
svg path:nth-child(3) {
  animation-delay: .5s; 
}
@keyframes colored {
  from {
    fill: #9900ff
  }
  to {
    fill: #2b84ea
  }
}

#btnnews {
  padding: 1rem;
  height: auto;
  width: auto;
}
.btn_wrapper {
  display: flex;
  align-items: center;
  
}
.line {
  border: solid #fff 1px;
  width: 10rem;
  height: .1rem;
  margin-left: 1rem;

}
.m0 {
  margin: 0;
}
.mt {
  margin-top: 2rem;
}



.animaed-wifi {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.titles_wrapper {
  padding-top: 6rem;
}




@media (max-width: 1240px) {
  .sdas {
    max-width: 900px;
  }
  .animaed-wifi {
    display: none;
  }
}