* {
  box-sizing: border-box;
}
.nav_items_con  {
  display: flex;
  justify-content: space-between;
  width: 27rem;
  align-items: flex-end;
}
.nav_items {
  width: 5rem;
  text-align: center;
  height: 3rem;
  padding-top: 15px;
  background-color: transparent;
  cursor: pointer;
  text-decoration: none;
  color: #fff;
  

}

.nav_items.active {
  

  border-bottom: solid #fff 2px;
}
.logopng {
  height: 4rem;
}
.logo {
  padding-top: 1rem;
}
.wrapper_nav {
  position: fixed;
  width: 100%;
}
.nav_con {
  
  max-width: 1170px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}